<template>
    <div class="modal" id="createConference" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog d-flex flex-column-reverse">
            <div class="p-2">
                <div class="modal-content">
                    <div class="modal-header">
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <!-- 회의 생성/참가 버튼, 폼 -->
                    <div class="modal-body">
                        <!-- 회의실 생성/참가 버튼 -->
                        <div class="input-group mb-3">
                            <div class="input-group-prepend">
                                <span class="btn btn-outline-secondary btn-md" @click="joinOrCreate" type="submit">회의생성</span>
                            </div>
                            <input type="text" class="form-control"  id="exampleFormControlInput1" aria-label="Amount (to the nearest dollar)">

                            <!-- 회의 참가 -->
                            <div class="input-group-append" v-if="join_or_create">
                                 <span  type="submit" id="button_regist" data-bs-dismiss="modal"> 
                                    <router-link to="/chat" class="btn btn-outline-secondary btn-md" type="submit">
                                        {{msg_btn_right}} 
                                    </router-link>
                                 </span>
                            </div>
                            <!-- 회의 참가 -->

                            <!-- 회의 주소 복사 -->
                            <div class="input-group-append" v-if="!join_or_create">
                                <span class="btn btn-outline-secondary btn-md"  type="submit"> {{msg_btn_right}} </span>
                            </div>
                            <!-- 회의 주소 복사 -->

                         </div>
                        <!-- 회의실 생성/참가 버튼 -->

                        <!-- 회의실 생성 폼 -->
                        <div class="d-flex align-items-end flex-column" v-if="!join_or_create">
                            <div class="form-group w-100">
                                <form>
                                    <div class="form-group p-2">
                                        <label for="exampleFormControlInput1" class="p-1">회의장 이름 입력</label>
                                        <input type="email" class="form-control" id="exampleFormControlInput1" placeholder="회의장 이름 입력">
                                    </div>
                                    
                                    <div class="form-group p-2">
                                        <label for="exampleFormControlSelect1" class="p-1">최대 인원수 입력</label>
                                        <select class="form-control" id="exampleFormControlSelect1">
                                            <option v-for=" num_participant in 9 " :key="num_participant" > {{ num_participant }}</option>
                                        </select>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <!-- 회의실 생성 폼 --> 
                    </div>
                    <!-- 회의 생성/참가 버튼, 폼 -->
                         

                    <!-- 뒤로가기/생성하기 버튼 -->
                    <div class="modal-footer">
                        <!-- 회의 참가시 뒤로가기 버튼의 역할 -->
                            <button class="btn btn-outline-secondary btn-md d-flex justify-content-end " id="button_regist"  @click="joinOrCreate" v-if="join_or_create"> 
                                {{msg_btn_bottom}} 
                            </button>
                        <!-- 회의 생성시 다음 페이지로 라우팅, 모달 닫기 -->
                                   
                        <span>
                            <router-link to="/chat"> 
                            
                                      
                                <button class="btn btn-outline-secondary btn-md d-flex justify-content-end " id="button_regist"  @click="joinOrCreate" type="submit" v-if="!join_or_create"  data-bs-dismiss="modal"> 
                                    {{msg_btn_bottom}}
                                </button>
                            </router-link>
                        </span>
                    </div>
                    <!-- 뒤로가기/생성하기 버튼 -->
                </div>

            </div>
        </div>
    </div>

</template>

<script>
export default {
  name: "joinConference",
  components: {
  },
  data(){
      return {
          join_or_create : true, 
          msg_btn_right:'참가',
          msg_btn_bottom:'뒤로가기'
      }
  },
  methods: {
    joinOrCreate(){
        if(this.join_or_create == true){
            this.msg_btn_bottom = '생성하기';
            this.msg_btn_right = '복사';
        }else{
            this.msg_btn_right = '참가';
            this.msg_btn_bottom = '뒤로가기';
        }
        this.join_or_create = !this.join_or_create;
    }
  }
};
</script>

<style scoped>
    h3{
        text-align: left;
    }

    p {
        display: inline-block;
    }

    .tab-pane{
        margin-top: 20px;
    }
    
    button {
        margin-left: 10px;
    }

    img {
        width: 140px;
        margin-bottom: 20px;
        max-height: 140px;
        border-radius: 30%;
    }

</style>
