<template>
          <div class="form-group">
            <label for="id"> &nbsp; ID</label>
            <Field id="input_area" name="id" type="id" class="form-control" v-model="user_id" />
            <ErrorMessage name="id" class="error-feedback" />
          </div>

        <div class="form-group">
            <label for="username"> &nbsp; 이름</label>
            <Field id="input_area" name="username" type="text" class="form-control" v-model="user_name" />
            <ErrorMessage name="username" class="error-feedback" />
        </div>

       <div class="form-group">
            <label for="email"> &nbsp; 이메일</label>
            <Field id="input_area" name="email" type="text" class="form-control" v-model="user_email" />
            <ErrorMessage name="email" class="error-feedback" />
       </div>   

       <div class="form-group">
            <!-- <label for="email"> &nbsp; 이메일</label> -->
            <div class="row">
                <div class="col-6">
                  <!-- <Field id="input_area" name="email" type="text" class="form-control" v-model="user_email" />
                  <ErrorMessage name="email" class="error-feedback" /> -->
                </div>
                <!-- <div class="col">
                    <button class="w-100 btn btn-sm" on-click="#" type="button" id="button_regist">인증번호 전송</button>
                </div>     -->
                <div class="btn-group btn-group" role="group">
                  <button class="btn btn-light btn-block"  @click="FindPW" id="home-tab" data-bs-toggle="tab" type="button" role="tab" aria-controls="home" aria-selected="true">
                        PW 찾기
                  </button>
                  <button @click="this.$router.go()" id="home-tab" class="btn btn-light btn-block" data-bs-toggle="tab" type="button" role="tab" aria-controls="home" aria-selected="true">
                        닫기
                  </button>
                </div>
            </div>
          </div>

        <!-- class, name 지정 안해둠 -->
         <!-- <div class="form-group">
            <div class="row">
                  <label for="email">인증번호 입력</label>
                  <div class="col-6">
                      <Field name="email" type="text" class="form-control" />
                      <ErrorMessage name="email" class="error-feedback" />
                  </div>              
                  <div class="col btn-group" role="group" aria-label="Basic example">
                    <button type="button" class="w-45 btn btn-sm">확인</button>
                    <button type="button" class="w-45 btn btn-sm">재전송</button>
                  </div>
            </div>
        </div> -->

</template>

<script>
import {  Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";
import axios from 'axios';

export default {
  name: "SearchPW",
  
  methods:{
    FindPW(){
      axios.post('/FindUserPW', {
          user_id : this.user_id,
          user_name : this.user_name,
          user_email : this.user_email
      }).then(response => {
        if(response.data){
          alert("회원님의 PW은(는)" + response.data + "입니다.")
        }else{
          alert("조회하신 정보의 회원 정보가 없습니다.")
        }
      })
    }
  },
  components: {
    // Form,
    Field,
    ErrorMessage,
  },
  data() {
    const schema = yup.object().shape({
      username: yup
        .string()
        .required("Username is required!")
        .min(3, "Must be at least 3 characters!")
        .max(20, "Must be maximum 20 characters!"),
      id: yup
        .string()
        .required("Id is required!")
        .min(6, "Must be at least 6 characters!")
        .max(40, "Must be maximum 40 characters!"),
      email: yup
        .string()
        .required("Email is required!")
        .email("Email is invalid!")
        .max(50, "Must be maximum 50 characters!"),
    });
    return {
      successful: false,
      loading: false,
      message: "",
      schema,
    };
  },
  computed: {
    }
  };
</script>

<style scoped>
p{
  margin-bottom: 0px;
}

.authCheckForm{ 
    display: block;
    width: 70%;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

#input_area{
  margin-top: 10px;
  margin-bottom: 10px;
}

#home-tab{
  margin-top: 10px;
}
</style>
