<template>
<div id="contain">
  <div class="col-lg-12">
    <div id="regist_tab">
      <h3 id="regist_title">Regist</h3>
    </div>
    
    <div class="card card-container">
      <img
        id="profile-img"
        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
        class="profile-img-card"
      />
      <Form @submit="handleRegister" :validation-schema="schema">
        <div v-if="!successful">
          <div class="form-group">
            <label for="id">ID</label>
            <Field name="id" type="text" class="form-control" v-model="user_id" />
            <ErrorMessage name="id" class="error-feedback" />
          </div>
          <div class="form-group">
            <label for="username">Name</label>
            <Field name="username" type="username" class="form-control" v-model="user_name" />
            <ErrorMessage name="username" class="error-feedback" />
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <Field name="password" type="password" class="form-control" autocomplete="off" v-model="user_pw" />
            <ErrorMessage name="password" class="error-feedback" />
          </div>
          <div class="form-group">
            <label for="email">Email</label>
            <Field name="email" type="email" class="form-control" v-model="user_email" />
            <ErrorMessage name="email" class="error-feedback" />
          </div>

          <div class="form-group" id="buttons">
            <router-link to="/login">
              <button type="button" id="button_cancel" class="w-47 btn btn-sm">Cancel</button>
            </router-link>
            <button type="button" @click="submit" id="button_signup" class="w-47 btn btn-sm">SignUp</button>
          </div>
        </div>
      </Form>

      <div
        v-if="message"
        class="alert"
        :class="successful ? 'alert-success' : 'alert-danger'"
      >
        {{ message }}
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { Form, Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";
import axios from 'axios';
export default {
  name: "RegisterPage",
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    const schema = yup.object().shape({
      username: yup
        .string()
        .required("Username is required!")
        .min(3, "Must be at least 3 characters!")
        .max(20, "Must be maximum 20 characters!"),
      id: yup
        .string()
        .matches( /[a-zA-Z0-9]/g , "You only insert alphabet or number!")
        .required("Id is required!")
        .min(6, "Must be at least 6 characters!")
        .max(40, "Must be maximum 40 characters!"),
      email: yup
        .string()
        .required("Email is required!")
        .email("Email is invalid!")
        .max(50, "Must be maximum 50 characters!"),
      password: yup
        .string()
        .required("Password is required!")
        .min(6, "Must be at least 6 characters!")
        .max(40, "Must be maximum 40 characters!"),
    });
    return {
      successful: false,
      loading: false,
      message: "",
      schema,
    };
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    handleRegister(user) {
      this.message = "";
      this.successful = false;
      this.loading = true;
      this.$store.dispatch("auth/register", user).then(
        (data) => {
          this.message = data.message;
          this.successful = true;
          this.loading = false;
        },
        (error) => {
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.successful = false;
          this.loading = false;
        }
      );
    },

    submit(){
      axios.post('/insertUser', {
          user_id : this.user_id,
          user_name : this.user_name,
          user_pw : this.user_pw,
          user_email : this.user_email
      }).then(response => {
         if(response.status){
           alert("회원가입에 성공하셨습니다.")
           this.$router.push('/login')
         }else{
           alert("회원가입에 실패하셨습니다. 다시 시도해주십시오.")
         }
      }).catch(err => {
         console.log(err)
      })
    }
  },

};
</script>

<style scoped>
label {
  display: block;
  margin-top: 10px;
}
.card-container.card {
    margin-top: 20px;
    max-width: 350px !important;
    padding: 40px 40px;
    width: 130%;
}


 
.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  /* margin: 0 auto 25px; */
  /* align-self: center; */
  /* 세로 위치 조정 */
  /* margin-top: 8%;  */
  /* 세로 위치 조정 */
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  /* -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3); */
  min-width: min-content;
  align-items: center;
  position: absolute;
  /* 가로 위치 조정 */
  /* left: 50% */
  /* 가로 위치 조정 */
}
.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.error-feedback {
  color: red;
}
#button_signup {
  display: inline-block;
  flex: 1;
  margin-right: 6px;
  width: 108px;
  font-size: 11px; 
  color: white;
  background: #00C9BE;
}
  #button_cancel {
    display: inline-block;
    flex: 1;
    margin-left:2px;
    margin-right: 6px;
    width: 108px;
    font-size: 11px; 
    background: #bbbbbb;
    color: white;
  }

  #regist_title {
        margin-left: 15px;
        /* align-self: center;
        flex: 6; */
    }
  #regist_tab {
        margin-top: 8%;
        /* left: 50%; */
        display: flex;
        flex-direction: row;
        flex:1;
        border-left-style: solid;
        border-left-width: 5px;
        border-left-color: #00C9BE;
        height: 35px;
    }

  #buttons{
    margin-top: 20px;
  }
  #contain{
    margin-left: 50%;
