                </div>
            </div>
            <!-- 클릭한 요소에 따라 버튼 전환 -->
            <!-- <div class="modal-footer">
              <button type="button" @click="authCheck" class="btn btn-light btn-block" id="login_button"> {{ footer_msg }} test </button>
            </div> -->
